import { useEffect, useState } from 'react';
const querystring = require('querystring');

function generateRandomString(length) {
    var result           = '';
    var characters       = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
    var charactersLength = characters.length;
    for ( var i = 0; i < length; i++ ) {
        result += characters.charAt(Math.floor(Math.random() * charactersLength));
    }
    return result;
}

function App() {
    const CLIENT_ID = '550d45a27d884184a09e9d159cc04697';
    const CLIENT_SECRET = '49dd56b682594d34aa4330d22993762a';
    const REDIRECT_URI = 'http://localhost:3000/';
    const AUTH_ENDPOINT = 'https://accounts.spotify.com/authorize';
    const RESPONSE_TYPE = 'code';
    const QUERY_PARAMS = querystring.stringify({
        response_type: 'code',
        client_id: CLIENT_ID,
        scope: 'user-top-read',
        redirect_uri: REDIRECT_URI,
        state: generateRandomString(16),
    });

    const API_BASE = 'https://api.spotify.com/v1/';

    const [token, setToken] = useState('');
    const [topTracks, setTopTracks] = useState([]);

    useEffect(() => {
        const params = new Proxy(new URLSearchParams(window.location.search), {
            get: (searchParams, prop) => searchParams.get(prop),
        });

        let body = querystring.stringify({
            'grant_type': 'authorization_code',
            'code': params.code,
            'redirect_uri': REDIRECT_URI,
        });

        console.log(params.code);
        let url = 'http://accounts.spotify.com/api/token/?' + body;
        fetch(url, {
            method: 'POST',
            headers: {
                Authorization: 'Basic ' + (btoa(CLIENT_ID + ':' + CLIENT_SECRET)),
                'Content-Type': 'application/x-www-form-urlencoded',
            },
        }).then(res => console.log(res));

    }, [])

    const logout = () => {
        setToken('');
    }

    const getTopTracks = async () => {
        console.log(token);

        const url = 'https://api.spotify.com/v1/me/top/track/';
        const { data } = await fetch(url, {
            method: 'GET',
            headers: {
                Authorization: `Bearer ${token}`,
            },
        }).then(res => {
            console.log(res);
        });

        setTopTracks(data.items);
    }

    return (
        <div>
            <h1>Playlist Automator</h1>
            { !token ?
                     <a href={`${AUTH_ENDPOINT}?${QUERY_PARAMS}`}>Login
                        to Spotify</a>
                     : <button onClick={logout}>Logout</button> }

            <button onClick={ getTopTracks }>Get top tracks</button>
            { !topTracks ? '' : topTracks }
        </div>
    );
}

export default App;
